import React, { Component}  from 'react'
import { Button, Form, Col, Alert } from 'react-bootstrap';
import { Route, NavLink, Link,useHistory } from 'react-router-dom'
import { connect } from 'react-redux';
import agent from '../../agent'
import ListErrors from '../layout/ListErrors';
import user from "../../models/user";
import Loader from '../layout/Loader';
import {
    UPDATE_FIELD_AUTH,
    LOGIN,
    LOGIN_PAGE_UNLOADED
} from '../../constants/actionTypes';

class Login extends Component {
   // let history = useHistory();

    constructor(props) {
        super(props);
        this.state ={isShowLoader:false};
        this.changeEmail = ev => {this.props.onChangeEmail(ev.target.value)};
        this.changePassword = ev => {this.props.onChangePassword(ev.target.value)};
    }
    submitForm=ev=> {
        ev.preventDefault();
        this.setState({isShowLoader:true});
        user.Auth.login(this.props.username, this.props.password).then(res=>{
            this.setState({isShowLoader:false});
            console.log(res);
            this.props.onSubmit(res.body);
        })
    };

    componentDidUpdate(){
        if(this.props.isLoginSuccess){
            this.props.history.push('/')
        }
        
    }
    
    componentWillUnmount() {
        this.props.onUnload();
      }
    render() {
        const username = this.props.username;
        const password = this.props.password;
        return (
            <>
            <div className="login-div">
            <Loader show={this.state.isShowLoader} />
        {/* <Alert key={this.state.errormsg} variant="danger">{this.state.errormsg}</Alert> */}
            <ListErrors errors={this.props.errors} statusCode={this.props.statusCode}/>
                <Form className="login-form" onSubmit={this.submitForm}>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicEmail">
                            <Form.Label>Username</Form.Label>
                            <Form.Control type="text" placeholder="Enter username" value={username} onChange={this.changeEmail} required/>
                        </Form.Group>
                    </Form.Row>
                    <Form.Row>
                        <Form.Group as={Col} controlId="formBasicPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" placeholder="Password" value={password}  onChange={this.changePassword} required/>
                        </Form.Group>

                    </Form.Row>
                    <Form.Row >
                        <Col>
                        <Button variant="primary" disabled={this.props.inProgress} type="submit">{this.props.inProgress ? 'Loading' : "Login"}</Button>

                        </Col>
                    </Form.Row>
                    {/* <Form.Row>
                        <Link as={Col} to="/registration" > SignUp</Link>
                    </Form.Row> */}

                </Form>
            </div>
            </>
        )
    }
}
const mapStateToProps = state => ({ ...state.auth });
const mapDispatchToProps = dispatch => ({
    onChangeEmail: value =>
        dispatch({ type: UPDATE_FIELD_AUTH, key: 'username', value }),
    onChangePassword: value =>
        dispatch({ type: UPDATE_FIELD_AUTH, key: 'password', value }),
    onSubmit: (data) =>
        dispatch({ type: LOGIN, payload: data }),
    onUnload: () =>
        dispatch({ type: LOGIN_PAGE_UNLOADED })
});

export default connect(mapStateToProps, mapDispatchToProps)(Login);
